<template>
  <mdb-container class="converter-container" fluid>
    <mdb-row class="converter-box">
      <mdb-row class="converter-row">
        <mdb-col
          col="12"
          class="row flex-nowrap justify-content-center align-items-center order-3 order-lg-1 px-0 py-1"
        >
          <mdb-col col="auto" class="d-none d-lg-block p-0">
            <div class="converter-text-col text-light text-shadow">From</div>
          </mdb-col>

          <mdb-col col="auto" class="py-0 px-3">
            <mdb-btn
              @click="swapCurrencyCodes"
              color="primary"
              class="swap-currency-codes-btn box-shadow"
              size="lg"
              floating
            >
              <i class="fas fa-exchange-alt text-light"></i>
            </mdb-btn>
          </mdb-col>

          <mdb-col col="auto" class="d-none d-lg-block p-0">
            <div class="converter-text-col text-light text-shadow">To</div>
          </mdb-col>
        </mdb-col>

        <mdb-col
          col="12"
          lg="auto"
          class="order-1 order-lg-2 row justify-content-center align-items-center px-0 pt-0 pb-1 p-lg-0"
        >
          <div class="input-group-label text-light d-inline-block d-lg-none">
            From
          </div>

          <div class="input-group input-group-lg box-shadow">
            <input v-model="baseValue" class="form-control" />

            <multiselect
              v-model="baseCurrency"
              :options="currencyCodes"
              :searchable="true"
              :canDeselect="false"
              noResultsText="???"
              :loading="!currencyCodes.length"
              :class="!currencyCodes.length ? 'is-loading' : ''"
            />
          </div>
        </mdb-col>

        <mdb-col col="auto" class="d-none d-lg-block order-lg-3 px-4">
          <i
            class="fas fa-long-arrow-alt-right text-primary text-shadow"
            style="font-size: 4rem;"
          ></i>
        </mdb-col>

        <mdb-col
          col="12"
          lg="auto"
          class="order-2 order-lg-4 row justify-content-center align-items-center p-0"
        >
          <div class="input-group-label text-light d-inline-block d-lg-none">
            To
          </div>

          <div class="input-group input-group-lg box-shadow">
            <input :value="targetValue" class="form-control" disabled />

            <multiselect
              v-model="targetCurrency"
              :options="currencyCodes"
              :searchable="true"
              :canDeselect="false"
              noResultsText="???"
              :loading="!currencyCodes.length"
              :class="!currencyCodes.length ? 'is-loading' : ''"
            />
          </div>
        </mdb-col>

        <mdb-col col="12" class="order-last row justify-content-center">
          <div class="input-group text-light text-shadow">
            <mdb-checkbox
              v-model="rememberChosenCurrencies"
              label="Remember chosen currencies for your next visit"
            />
          </div>
        </mdb-col>
      </mdb-row>
    </mdb-row>
  </mdb-container>
</template>

<script>
import useCurrencyToCurrency from "@/composables/useCurrencyToCurrency";

export default {
  name: "CurrencyToCurrency",

  setup() {
    const {
      currencyCodes,
      rememberChosenCurrencies,
      baseCurrency,
      targetCurrency,
      baseValue,
      targetValue,
      swapCurrencyCodes
    } = useCurrencyToCurrency();

    return {
      currencyCodes,
      rememberChosenCurrencies,
      baseCurrency,
      targetCurrency,
      baseValue,
      targetValue,
      swapCurrencyCodes
    };
  }
};
</script>

<style lang="scss">
.converter-container {
  .converter-box {
    padding: 0;
    position: absolute;
    top: 43%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    transform: translateY(-50%);
    width: 100%;
    justify-content: center;

    .converter-row {
      padding: 0;
      margin: 0;
      align-items: center;
      justify-content: center;
    }

    .converter-text-col {
      width: 250px;
      font-size: 1.25rem;
      text-align: center;
    }

    .swap-currency-codes-btn {
      padding: 0;
      margin: 0;
    }

    .swap-currency-codes-btn > .fas.fa-exchange-alt {
      padding: 0;
      margin: 0;
      text-shadow: none;
      font-size: 1.8rem;
    }

    .input-group {
      padding: 0;
      margin: 0;
      width: auto;
      flex-wrap: nowrap;
      white-space: nowrap;
    }

    .input-group-label {
      font-size: 1.3rem;
      width: 60px;
      padding: 0;
      margin: 0;
    }

    .form-control {
      width: 150px;
      font-size: 1.2rem;
      text-align: center;
    }

    .multiselect {
      width: 100px;
    }

    .multiselect-input {
      border-radius: 0 0.3rem 0.3rem 0;
    }

    .multiselect-search input {
      font-size: 1.2rem;
    }

    .multiselect-single-label {
      font-size: 1.2rem;
    }

    .multiselect-option {
      font-size: 1rem;
    }

    .multiselect-clear {
      display: none;
    }
  }
}
</style>
