import { ref, computed, watch } from "vue";
import { useStore } from "vuex";

const DEFAULT_BASE_CURRENCY = "EUR";
const DEFAULT_TARGET_CURRENCY = "USD";

export default () => {
  const store = useStore();

  const currencyCodes = computed(() => store.getters.sortedCurrencyCodes);

  const rememberChosenCurrencies = ref(
    localStorage.converterRememberChosenCurrencies === "true"
  );

  const baseCurrency = ref(
    rememberChosenCurrencies.value
      ? localStorage.converterBaseCurrency || DEFAULT_BASE_CURRENCY
      : DEFAULT_BASE_CURRENCY
  );
  const targetCurrency = ref(
    rememberChosenCurrencies.value
      ? localStorage.converterTargetCurrency || DEFAULT_TARGET_CURRENCY
      : DEFAULT_TARGET_CURRENCY
  );

  const baseValue = ref(1);
  const targetValue = computed(() =>
    store.getters.getConvertedCurrencyValue(
      baseValue.value,
      baseCurrency.value,
      targetCurrency.value
    )
  );

  const swapCurrencyCodes = () => {
    [baseCurrency.value, targetCurrency.value] = [
      targetCurrency.value,
      baseCurrency.value
    ];
  };

  const storeRememberChosenCurrencies = () => {
    localStorage.converterRememberChosenCurrencies =
      rememberChosenCurrencies.value;
  };
  const storeBaseCurrency = () => {
    if (rememberChosenCurrencies.value) {
      localStorage.converterBaseCurrency = baseCurrency.value;
    }
  };
  const storeTargetCurrency = () => {
    if (rememberChosenCurrencies.value) {
      localStorage.converterTargetCurrency = targetCurrency.value;
    }
  };

  const validateBaseValue = (newValue, oldValue) => {
    if (isNaN(newValue) || newValue < 0 || newValue > 999999999) {
      baseValue.value = oldValue;
    }
  };

  watch(baseValue, validateBaseValue);
  watch(baseCurrency, storeBaseCurrency);
  watch(targetCurrency, storeTargetCurrency);
  watch(rememberChosenCurrencies, () => {
    storeRememberChosenCurrencies();
    storeBaseCurrency();
    storeTargetCurrency();
  });

  return {
    currencyCodes,
    rememberChosenCurrencies,
    baseCurrency,
    targetCurrency,
    baseValue,
    targetValue,
    swapCurrencyCodes
  };
};
